<i18n>
{
  "en": {
    "payBill": "Payment Order",
    "unit": "￥",
    "Unpaid": "Unpaid",
    "returnAccount": "Please select the payment account number.",
    "item": "Cancel | Confirm",
    "WeChat": "WeChat",
    "Alipay": "Alipay",
    "Balance": "Balance",
    "Pay": "Pay"
  },
  "zh": {
    "payBill": "支付订单",
    "unit": "元",
    "Unpaid": "待支付",
    "returnAccount": "请选择支付账号",
    "item": "取消 | 确定",
    "WeChat": "微信",
    "Alipay": "支付宝",
    "Balance": "余额支付",
    "Pay": "确认支付"
  }
}
</i18n>

<template>
  <div class="bill">
    <head-top :title='$t("payBill")' :go-back="true"></head-top>
    <div class="container">
      <div class="bg">
          <div class="count">
            <span class="num">{{data && data.fee / 100}}</span>
            <strong>{{ $t("unit") }}</strong>
          </div>
          <div class="ispay">
            {{ $t("Unpaid") }}
          </div>
      </div>
      <div class="pay">
        <div class="tips">{{ $t("returnAccount") }}</div>
        <ul>
          <li v-for="(item, index) in way" :key="item.type" @click="selectWay(item, index)" class="bomPixel">
            <span class="icon"><img :src="item.icon" alt=""></span>
            <span class="text">{{item.text}}</span>
            <span class="circle">
                <img v-show="index === activePayIndex && (Version === 1 || Version === 3)" src="../../assets/wallet_images/click.png" alt="" >
                <img v-show="index === activePayIndex && Version === 2" src="../../assets/synerMax-skin/click@3x.png" alt="">
                <img v-show="index !== activePayIndex" src="../../assets/wallet_images/quan-default.png" alt="">
            </span>
          </li>
        </ul>
      </div>
      <div :class="['btn', {cwhite:Version === 2}]" @click="doWithdraw">
        {{ $t("Pay") }}
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import headTop from '../../components/Header/index'
import messageBox from '../../components/MessageBox/index'
export default {
  name: 'bill',
  data () {
    return {
      activePayIndex: 0,
      way: [
        {
          icon: require('../../assets/wallet_images/alipay.png'),
          text: this.$t('Alipay'),
          type: 'alipay'
        },
        {
          icon: require('../../assets/wallet_images/wechat.png'),
          text: this.$t('WeChat'),
          type: 'wepay'
        },
        {
          icon: require('../../assets/wallet_images/rmb.png'),
          text: this.$t('Balance'),
          type: 'balancepay'
        }
      ],
      data: {} // 订单数据
    }
  },

  components: {
    headTop,
    messageBox
  },

  mounted () {
    console.log(this.$route.params.item)
    this.data = this.$route.params.item

    this.dataInteractionResponse(res => {
      switch (res.msgType) {
        // 第三方支付返回
        case '0x5232':
          if (res.result) {
            // 查询单个订单
            this.dataInteractionRequest({
              msgType: '0x5223',
              no: res.data.out_trade_no
            })
            if (this.ISDEBUG) {
              let str = '{"data":{"duration":"","no":"LENT20181129164630376257675554","pay_state":4,"balance_deduction":0,"return_time":"","lent_adress":"广东省广州市天河区建中路3号","fee":100,"lent_time":"2018-11-29 16:46:30","need_pay_fee":0,"return_adress":"","discount_fee":0},"result":true,"msgType":"0x5224"}'
              window.dataInteractionResponse(str)
            }
          } else {
            this.setGlobalToast({
              bol: true,
              zh: '支付失败',
              en: 'payment failure'
            })
          }
          break
        case '0x5234':
          // 余额支付返回
          if (res.result) {
            // 查询单个订单
            this.dataInteractionRequest({
              msgType: '0x5223',
              no: res.data.no
            })
            if (this.ISDEBUG) {
              let str = '{"data":{"duration":"","no":"LENT20181129164630376257675554","pay_state":4,"balance_deduction":0,"return_time":"2018-11-29 16:49:35","lent_adress":"广东省广州市天河区建中路3号","fee":100,"lent_time":"2018-11-29 16:46:30","need_pay_fee":0,"return_adress":"","discount_fee":0},"result":true,"msgType":"0x5224"}'
              window.dataInteractionResponse(str)
            }
          } else {
            this.setGlobalToast({
              bol: true,
              zh: '支付失败',
              en: 'payment failure'
            })
          }
          break
        case '0x5224':
          // 查询单个订单返回
          if (res.result) {
            this.$router.push({
              name: 'orderSuccess',
              params: {
                data: res.data
              }
            })
          } else {
            this.setGlobalToast({
              bol: true,
              zh: '查询订单失败',
              en: 'query order failed!'
            })
          }
          break
      }
    })
  },
  methods: {
    ...mapActions([
      'setGlobalBox', 'setGlobalToast', 'loadingAlert'
    ]),
    selectWay (item, index) {
      this.activePayIndex = index
    },
    // 确认支付
    doWithdraw () {
      if (!this.ISDEBUG) {
        this.loadingAlert(true)
      }
      switch (this.activePayIndex) {
        case 0:
          // 支付宝支付
          this.dataInteractionRequest({
            msgType: '0x5231',
            platform: 2,
            no: this.data && this.data.no,
            fee: this.data && this.data.fee,
            currency: 'CNY',
            description: 'Incharge-订单支付'
          })
          if (this.ISDEBUG) {
            let str = '{"data":{"platform":2,"bank_type":"招商银行","total_fee":100,"out_trade_no":"","transaction_id":"LENT2018120509545933126190284T","trade_type":"","time_end":"","trade_state":"SUCCESS","cash_fee":"","coupon_fee":"","coupon_count":"","trade_state_desc":""},"result":true,"msgType":"0x5232"}'
            window.dataInteractionResponse(str)
          }
          break
        case 1:
          // 微信支付
          this.dataInteractionRequest({
            msgType: '0x5231',
            platform: 1,
            no: this.data && this.data.no,
            fee: this.data && this.data.fee,
            currency: 'CNY',
            description: 'Incharge-订单支付'
          })
          if (this.ISDEBUG) {
            let str = '{"data":{"platform":1,"bank_type":"招商银行","total_fee":100,"out_trade_no":"","transaction_id":"LENT2018120509545933126190284T","trade_type":"","time_end":"","trade_state":"SUCCESS","cash_fee":"","coupon_fee":"","coupon_count":"","trade_state_desc":""},"result":true,"msgType":"0x5232"}'
            window.dataInteractionResponse(str)
          }
          break
        case 2:
          // 余额支付
          this.dataInteractionRequest({
            msgType: '0x5233',
            no: this.data && this.data.no
          })
          if (this.ISDEBUG) {
            let str = '{"data":{"no":"LENT2018120509545933126190284T"},"result":true,"msgType":"0x5234"}'
            window.dataInteractionResponse(str)
          }
          break
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.bill{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .bg{
      .wh(691px,284px);
      margin: 20px auto 40px auto;
      background: url('../../assets/wallet_images/bg.png');
      background-size: cover;
      padding-top: 66px;
      font-size: 32px;
      .num{
        font-size: 82px;
        position: relative;
        strong{
          font-size: 30px;
          position: absolute;
          bottom: 10px;
          right: -50px;
        }
      }
      .ispay{
        margin-top: 20px;
        font-size: 30px;
        color: #AAAAAA;
      }
    }
    .pay{
      .tips{
        .wh-l(100%, 80px);
        text-align: left;
        padding-left: 30px;
        font-size: 30px;
        color: #aaaaaa;
      }
      ul{
        margin-bottom: 10px;
        li{
          .wh-l(100%, 90px);
          position: relative;
          span{
            display: block;
            img{
              vertical-align: middle;
            }
            &.icon{
              .wh-f(15%,100%);
              img{
                .wh(44px,44px);
              }
            }
            &.text{
              .wh-f(70%,100%);
              text-align: left;
              font-size: 32px;
              strong{
                font-size: 18px;
                color: red;
              }
            }
            &.circle{
              .wh-f(15%,100%);
              img{
                .wh(30px,30px);
              }
            }
          }
        }
      }
    }
  }
   .btn{
    border-radius: 10px;
    font-size: 30px;
    .wh-l(690px,100px);
    background: @themeColor;
    color: #585858;
    position: absolute;
    left: 50%;
    margin-left: -345px;
    bottom: 50px;
    box-shadow: 0px 4px 25px @themeColor;
  }
  .cwhite{
    color: #FFF;
  }
}
</style>



